import { useState } from "react";
import { Formik, Form, Field, ErrorMessage as FormikErrorMessage } from "formik";
import * as Yup from "yup"; // Для валидации формы
import { Link } from "react-router-dom"; // Для ссылки на отдельную страницу

import useMarvelService from "../../services/MarvelService";
import ErrorMessage from "../errorMessage/ErrorMessage";

import "./findCharForm.scss";

const FindCharForm = () => {
    const [char, setChar] = useState(null);
    const { process, setProcess, getCharacterByName, clearError } = useMarvelService();

    const onCharLoaded = (char) => {
        setChar(char);
    };

    const updateChar = (name) => {
        clearError();
        getCharacterByName(name)
            .then(onCharLoaded)
            .then(() => setProcess("confirmed"));
    };

    const errorMessage =
        process === "error" ? (
            <div className="char__search-wrapper">
                <ErrorMessage />
            </div>
        ) : null;

    const results = !char ? null : char.length > 0 ? (
        <div className="char__search-wrapper">
            <div className="char__search-success">There is! Visit {char[0].name} page?</div>
            <Link
                to={`/characters/${char[0].id}`}
                className="button button__secondary"
                disabled={process === "loading"}
                onClick={onCharLoaded}
            >
                <div className="inner">To page</div>
            </Link>
        </div>
    ) : (
        <div className="char__search-error">
            The character was not found. Check the name and try again
        </div>
    );

    return (
        <div className="char__search-form">
            <Formik
                initialValues={{
                    charName: "",
                }}
                validationSchema={Yup.object({
                    charName: Yup.string().required("This field is required"),
                })}
                onSubmit={({ charName }) => {
                    updateChar(charName);
                }}
            >
                <Form>
                    <label className="char__search-label" htmlFor="charName">
                        Or find a character by name:
                    </label>
                    <div className="char__search-wrapper">
                        <Field id="charName" name="charName" type="text" placeholder="Enter name" />
                        <button
                            type="submit"
                            className="button button__main"
                            disabled={process === "loading"}
                        >
                            <div className="inner">Find</div>
                        </button>
                    </div>
                    <FormikErrorMessage
                        component="div"
                        className="char__search-error"
                        name="charName"
                    />
                </Form>
            </Formik>
            {results}
            {errorMessage}
        </div>
    );
};

export default FindCharForm;
